.Imgpic {
  display: block;
  max-width: 95%;
  height: 200px;
  margin: 20px auto;
  user-select: none;
}
.Imgpic2 {
  user-select: none;
  max-width:100%;height:auto
}
.img-fluid{max-width:100%;height:auto}
.Div, .img, .Header, .InfoRow, .Group  {
user-select: none;
}
.tablefilter {
  width: 100%;
  border-collapse: collapse;
}
table {
  width: 100%;
  border-collapse: collapse;
}
/* Zebra striping */
tr:nth-of-type(odd) {
  background: #eee;
}
th {
  background: #5181b8;
  color: white;
  font-weight: bold;
}
td, th {
  padding: 3px;
  border: 1px solid #ccc;
  text-align: left;
}
@media
only screen and (max-width: 760px),
(min-device-width: 768px) and (max-device-width: 1024px)  {

	/* Force table to not be like tables anymore */
	table, thead, tbody, th, td, tr {
		display: block;
	}

	/* Hide table headers (but not display: none;, for accessibility) */
	thead tr {
		position: absolute;
		top: -9999px;
		left: -9999px;
	}

	tr { border: 1px solid #ccc; }

	td {
		/* Behave  like a "row" */
		border: none;
		border-bottom: 1px solid #eee;
		position: relative;
		padding-left: 50%;
	}

	td:before {
		/* Now like a table header */
		position: absolute;
		/* Top/left values mimic padding */
		top: 3px;
		left: 3px;
		width: 45%;
		padding-right: 5px;
		white-space: nowrap;
	}

	/*
	Label the data
	*/
	td:nth-of-type(1):before { content: "Компания"; }
	td:nth-of-type(2):before { content: "Диск"; }
	td:nth-of-type(3):before { content: "ОЗУ"; }
	td:nth-of-type(4):before { content: "Кол-во CPU"; }
	td:nth-of-type(5):before { content: "Трафик"; }
	td:nth-of-type(6):before { content: "Цена"; }
	td:nth-of-type(7):before { content: "Виртуализация"; }
	td:nth-of-type(8):before { content: "Страна"; }
	td:nth-of-type(9):before { content: "Доп"; }
	td:nth-of-type(10):before { content: ""; }
}
